body {
	background-color: #f3a43d;
  }
  .App{
   position: absolute;
   left:38%;
   top:15%;
  }
  button {
	width: 70px;
	height: 70px;
	background-color: #017bff;
	border: 0px;
	margin: 2px;
	font-size: 20px;
	cursor: pointer;
	font-weight: 500;
	color: black;
	border-radius: 60px;
  }
  button:hover{
	background-color: #ff88c1;
  }
  .btn {
	box-shadow: 7px 6px 28px 1px rgba(0, 0, 0, 0.24);
	transition: 0.2s all;
  }
  .btn:active {
	transform: scale(0.98);
	box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24);
  }
  input {
	width: 250px;
	height: 40px;
	font-size: 40px;
	border: 0;
	border-radius: 10px;
	margin: 5%;
	font-weight: 1;
	text-align: right;
	background-color:white;
	color: black;
	margin-top: 50px;
	margin-bottom: 50px;
  }
  .calculator {
	background-color: white;
	border-radius: 20px;
	text-align: center;
	height:520px;
	width:300px;
	border-style: solid;
	border-width: 0.2px;
  }
  .numbers{
	background-color:#cccccc;
  }
  .operators{
	background-color:#e1882a;
	width:140px;
	margin-right: 1.2px;
  }